<template>
    <header id="header" class="mb-5">
        <div class="header-wrapper">
            <div class="container">
                <div class="row justify-content-center align-items-center mb-3">
                    <router-link to="/" id="brand" class="col-lg-3 mb-lg-0 mb-3 pe-lg-5 text-lg-start text-center">
                        <img src="../assets/images/logo-web.svg" alt="MYTick8" class="brand-img d-inline-block">
                    </router-link>
                    <div class="navbar-wrapper col-lg-9">
                        <nav class="navbar navbar-expand-lg mt-lg-0 mt-3">
                            <button class="navbar-toggler border-0" type="button" data-bs-toggle="collapse"
                                data-bs-target="#main-menu" aria-controls="main-menu" aria-expanded="false"
                                aria-label="Toggle navigation">
                                <span class="navbar-toggler-icon"></span>
                            </button>
                            <div class="collapse navbar-collapse order-lg-1 order-2" id="main-menu">
                                <ul class="navbar-nav me-auto mt-lg-0 mt-4">
                                    <li class="nav-item">
                                        <router-link class="nav-link active mx-lg-3" to="/">برنامه ها</router-link>
                                    </li>
                                    <li class="nav-item menu-item-has-children">
                                        <router-link class="nav-link mx-lg-3" to="/">پشتیبانی</router-link>
                                    </li>
                                    <li class="nav-item">
                                        <router-link class="nav-link mx-lg-3" to="/">درباره ما</router-link>
                                    </li>
                                    <li class="nav-item">
                                        <router-link class="nav-link mx-lg-3" to="/">تماس با ما</router-link>
                                    </li>
                                </ul>
                            </div>
                            <ul
                                class="nav left-btns justify-content-between align-items-center ms-auto order-lg-2  order-1">
                                <li v-if="userStore.isAuth" class="nav-item me-3">
                                    <router-link class="nav-link d-flex justify-content-center align-items-center"
                                        :to="{ name: 'panel_index' }">
                                        <img src="../assets/images/Profile.svg" alt="" class="me-2" width="14.3px"
                                            height="18.4px">
                                        <span>{{ userStore.user.name + " " + userStore.user.family }}</span>
                                    </router-link>
                                </li>
                                <li v-else class="nav-item me-3">
                                    <router-link :to="{ name: 'auth' }"
                                        class="nav-link d-flex justify-content-center align-items-center">
                                        <img src="../assets/images/Profile.svg" alt="" class="me-2" width="14.3px"
                                            height="18.4px">
                                        ورود / ثبت نام
                                    </router-link>
                                </li>
                                <li class="nav-item">
                                    <router-link class="nav-link d-flex justify-content-center align-items-center"
                                        :to="{ name: 'tracking.buy' }">
                                        <img src="../assets/images/Ticket.svg" alt="" class="me-2" width="24px"
                                            height="24px">
                                        پیگیری خرید
                                    </router-link>
                                </li>
                            </ul>
                        </nav>
                        <div id="search-bar">
                            <div class="row w-100 m-0">
                                <form role="search" @submit.prevent="" method="" action="" id="searchform"
                                    class="col-lg-6 p-0 pe-lg-2 mb-lg-0 mb-2">
                                    <input type="text" v-model.lazy.trim="q" class="searchform-input">
                                    <input type="button" id="searchsubmit" value="">
                                </form>
                                <div id="choose-city"
                                    class="col-lg-6 d-flex justify-content-center align-items-center p-1">
                                    <p class="choose-city-title d-flex justify-content-center align-items-center">
                                        <img src="../assets/images/Location.svg" alt="" width="18px" height="17.7px"
                                            class="me-1">
                                        استان خود را انتخاب کنید
                                    </p>
                                    <select v-model="province" class="w-50 ms-auto px-2 py-1">
                                        <option value="">انتخاب نمایید</option>
                                        <option :value="_province.id" v-for="(_province, key) in provinces" :key="key">
                                            {{ _province.title }}</option>
                                    </select>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div id="calendar">
                    <div class="d-flex align-items-center">
                        <img src="../assets/images/calendar.svg" alt="" class="me-2" width="18px" height="17px">
                        <p class="calendar-date">
                            <span>امروز:</span>
                            <span>{{ nowDate() }}</span>
                            /
                            <span dir="ltr">{{ nowDate() }}</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </header>
</template>
<script setup>
import { ref, onMounted, watch } from 'vue'
import homeApi from '@/axios/api'
import { useRouter } from "vue-router";
import { nowDate } from '@/utils/helpers';
import { useUserStore } from '../store/user'

const provinces = ref([]);
const q = ref();
const province = ref();
const userStore = useUserStore()
const router = useRouter()

onMounted(async () => {
    try {
        const response = await homeApi.get('/provinces');
        provinces.value = response.data.provinces
    } catch (err) {
        console.log(err.message)
    }
})
watch(q, (n, o) => {
    let query = {}
    if (n) {
        query.q = n.trim();
    }
    if (province.value) {
        query.province = province.value;
    }
    if (q.value || province.value) {
        router.push({
            name: 'search', query: query
        })
    }
})
watch(province, (n, o) => {
    let query = {}
    if (n) {
        query.province = n;
    }
    if (q.value) {
        query.q = q.value.trim();
    }
    if (q.value || province.value) {
        router.push({
            name: 'search', query: query
        })
    }
})
</script>
<style>
.active {
    color: green;
}

.mr-4 {
    margin-right: 10px;
    ;
}
</style>